.login > input {
    font-family: "Asap", sans-serif;
    display: block;
    border-radius: 5px;
    font-size: 14px;
    background: gainsboro;
    width: 100%;
    padding: 5px 10px;
    margin: 15px -10px;
  }
/*     1200px */
@media screen and (min-width: 1200px) {
    .main_section_register {
        padding-left: 150px;
        padding-right: 150px;
    }
}

/*     768px */
@media screen and (min-width: 768px) {
    .main_section_register {
        padding-left: 100px;
        padding-right: 100px;
    }

    .register {
        width: 500px;
        left: 50%;
        transform: translateX(-50%);
    }
}

/*     480px */
@media screen and (min-width: 480px) {
    .main_section_register {
        padding-top: 100px;
    }
}

/*     320px */
@media screen and (min-width: 320px) {
    .register {
        width: 300px;
        left: 50%;
        transform: translateX(-50%);
    }
}

  .main_head_login {
    background-repeat: no-repeat;
    background-size: 100%;
    height: 600px;
    position: relative;
  }
  
  .main_head_login::before {
    content: "";
    background: url("../../images/ryan2.jpg");
    background-size: cover;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.85;
  }
  
  .main_section_login {
    max-width: 1500px;
    margin: 0 auto;
    padding-left: 100px;
    padding-right: 100px;
    padding-top: 150px;
    position: relative;
    text-align: center;
  }
  
  .main_section_content_login {
    margin: 0 auto;
    padding-left: 30px;
  }
  
  .main_section_content_login h1 {
    color: white;
    font-weight: bold;
    font-size: 50px;
    line-height: 60px;
    padding-bottom: 40px;
  }

.login {
    overflow: hidden;
    background-color: white;
    padding: 40px 30px 30px 30px;
    border-radius: 10px;
    max-width: 100%;
    
    padding-left: 5%;
    padding-right: 5%;
}
  
  .login > h2 {
    font-family: "Asap", sans-serif;
    font-size: 26px;
    text-align: center;
    padding-bottom: 10px;
  }
  
  .login > button {
    font-family: "Asap", sans-serif;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    width: 80px;
    padding: 10px 0;
    margin-top: 10px;
    margin-left: -5px;
    border-radius: 5px;
    background-color: #f45b69;
  }
  
  .login > button:hover {
    background-color: #f24353;
  }
  